export const gslbschema = {
	properties: {
		gslbadcSettings: {
			type: "object",
			title: "Common NetScaler ADC Settings",
			properties: {
				"gslb.enabled": { type: "boolean", default: true },
				"gslb.nsPort": { type: "number", title: "Management Port for NetScaler ADC", default: 443, maximum: 65535, minimum: 1 },
				"gslb.nsProtocol": {
					type: "object", title: "NetScaler ADC Management Protocol",
					properties: { nsProtocol: { type: "string", default: "HTTPS", enum: ["HTTPS", "HTTP"] } }
				},
			},
		},
		entity: {
			type: "object",
			title: "Entity related",
			properties: {
				"gslb.entityPrefix": { type: "string", title: "NetScaler ADC Entity Prefix", default: "k8s" },
			}
		},
		platformSetting: {
			type: "object",
			title: "Platform Setting",
			properties: {
				"gslb.openshift": { type: "boolean", title: "Openshift", default: false },
			}
		},
		gslbnsicSettings: {
			type: "object",
			title: "NetScaler Gslb Ingress Controller Settings",
			required: ['gslb.localRegion', 'gslb.localCluster'],
			properties: {
				'gslb.image': { type: "string", title: "NetScaler Gslb Ingress Controller Image", default: "quay.io/citrix/citrix-k8s-ingress-controller:1.9.20" },
				'gslb.pullPolicy': {
					type: "object", title: "Image Pull Policy",
					properties: { pullPolicy: { type: "string", default: "IfNotPresent", enum: ["Always", "IfNotPresent", "Never"] } }
				},
				'gslb.kubernetesURL': { type: "string", title: "Kubernetes API-server URL", format: "url" },
				'gslb.license.accept': { type: "string", default: "Yes" },
				'gslb.logLevel': {
					type: "object", title: "Ingress Controller Log Level",
					properties: { logLevel: { type: "string", default: "INFO", enum: ["DEBUG", "INFO", "WARN", "ERROR", "TRACE"] } }
				},
				'gslb.localRegion': { type: "string", title: "Local Region" },
				'gslb.localCluster': { type: "string", title: "Local ClusterName" },
			}
		},
		siteSettings: {
			type: "object",
			title: "Site Settings",
			required: ['gslb.sitedata'],
			properties: {
				'gslb.sitedata': {
					type: "array", title: "Site Data",
					items: {
						type: "object",
						properties: {
							"siteName": { type: "string", title: "Site Name" },
							"secretName": { type: "string", title: "Secret Name" },
							"siteIp": { type: "string", title: "Site IP", format: "ipv4" },
							"siteRegion": { type: "string", title: "Site Region" }
						}
					},
				},
			}
		},
		crdsSettings: {
			type: "object",
			title: "NetScaler CRDs Settings",
			properties: {
				"gslb.crds.install": { type: "boolean", title: "Install NetScaler CRDs", default: true },
			},
			dependencies: {
				'gslb.crds.install': {
					oneOf: [
						{
							"properties": {
								'gslb.crds.install': {
									const: false
								}
							}
						},
						{
							"properties":
							{
								"gslb.crds.install": { const: true },
								"gslb.crds.retainOnDelete": { type: "boolean", title: "Retain NetScaler CRDs on delete", default: false }
							}
						}

					]
				}

			}
		},
	}
};

