export const nsncschema = {
  properties: {
    nsncSettings: {
      type: "object",
      title: "NetScaler Node Controller Settings",
      required: ["nsnc.nsIP", "nsnc.vtepIP", "nsnc.adcCredentialSecret", "nsnc.network", "nsnc.vxlan.id", "nsnc.vxlan.port"],
      properties: {
        "nsnc.enabled": { type: "boolean", title: "nsnc enable", default: true },
        "nsnc.image": { type: "string", title: "NetScaler Node Controller Image", default: "quay.io/citrix/citrix-k8s-node-controller:2.0.0" },
        "nsnc.pullPolicy": {
          type: "object", title: "Image Pull Policy",
          properties: { pullPolicy: { type: "string", default: "IfNotPresent", enum: ["Always", "IfNotPresent", "Never"] } }
        },
        'nsnc.license.accept': { type: "string", title: "Accept License", default: "Yes" },
        "nsnc.nsIP": { type: "string", title: "NetScaler ADC IP" },
        "nsnc.vtepIP": { type: "string", title: "NetScaler ADC SNIP", format: "ipv4" },
        "nsnc.adcCredentialSecret": { type: "string", title: "Secret Name" },
        "nsnc.vxlan.id": { type: "number", title: "VxLAN ID", default: 2, minimum: 2, maximum: 16777215 },
        "nsnc.vxlan.port": { type: "number", title: "VxLAN Port", minimum: 1, maximum: 65434 },
        "nsnc.network": { type: "string", title: "Network" },
      },
    }
  }
};

