import React from 'react';
import './App.css';
import { withTheme } from '@rjsf/core';
import { Theme as SemanticTheme } from '@rjsf/semantic-ui';

import yaml from 'js-yaml';
import SyntaxHighlighter from 'react-syntax-highlighter';
import { a11yDark } from 'react-syntax-highlighter/dist/esm/styles/hljs';
import { Header, Grid, Segment } from 'semantic-ui-react';
import { CopyToClipboard } from 'react-copy-to-clipboard';


const log = (type) => console.log.bind(console, type);
const Form = withTheme(SemanticTheme);

class HelmForm extends React.Component {
  constructor(props) {
    super(props);
    this.toYaml = this.toYaml.bind(this);
    console.log('constructor: formData', this.props.formData);

  }

  componentDidMount() {
    console.log('componentDidMount: formData', this.props.formData);
    console.log('componentDidMount: yamlStr', this.props.yamlStr);
    console.log('componentDidMount: formId', this.props.formId);
  }

  toYaml({ formData }) {
    //console.log({formData});
    console.log('toYaml formData: ' + { ...formData });

    if (formData.adcSettings) {
      if (formData.nsncSettings && formData.nsncSettings['nsnc.enabled']) {
        formData.nsncSettings['nsnc.nsIP'] = formData.adcSettings['nsic.nsIP']
        formData.nsncSettings['nsnc.adcCredentialSecret'] = formData.adcSettings['nsic.adcCredentialSecret']
        formData.nsncSettings['nsnc.license.accept'] = 'Yes'
      }
      if (formData.ipamSettings && formData.ipamSettings['ipam.enabled']) {
        formData.adcSettings['nsic.ipam'] = true
      } else {
        formData.adcSettings['nsic.ipam'] = false
      }
      if (formData.cpxSettings && formData.cpxSettings['cpx.enabled']) {
        formData.cpxSettings['cpx.nsic.required'] = true
        formData.cpxSettings['cpx.nsic.image'] = formData.nsicSettings['nsic.image']
        formData.cpxSettings['cpx.nsic.pullPolicy'] = formData.nsicSettings['nsic.pullPolicy']
        formData.cpxSettings['cpx.nsic.required'] = true
        formData.cpxSettings['cpx.license.accept'] = 'Yes'
        if (formData.platformSetting['nsic.openshift']) {
          formData.platformSetting['cpx.openshift'] = true
        }
        formData.cpxSettings['cpx.logLevel'] = formData.adcSettings['nsic.logLevel']
      }
    }
    if (formData.ingressGatewaySettings && formData.ingressGatewaySettings['iaIngress.netscalerCPX']) {
      formData.ingressGatewaySettings['iaIngress.ingressGateway.vserverIP'] = "nsip"
    }
    if (formData.ingressGatewaySettings && !formData.ingressGatewaySettings['iaIngress.netscalerCPX']) {
      if (formData.nsncSettings && formData.nsncSettings['nsnc.enabled']) {
        formData.nsncSettings['nsnc.adcCredentialSecret'] = "nslogin"
        formData.nsncSettings['nsnc.license.accept'] = 'Yes'
      }
    }
    if (formData.gslbadcSettings && formData.gslbadcSettings['gslb.enabled']) {
      if (formData.gslbplatformSetting) {
        if (formData.gslbplatformSetting['gslb.openshift']) {
          formData.nsicSettings['nsic.openshift'] = true
        } else {
          formData.nsicSettings['nsic.openshift'] = false
        }
      }
      if (formData.crdsSettings && formData.nsicSettings) {
        if (formData.crdsSettings['gslb.crds.install']) {
          formData.nsicSettings['nsic.crds.install'] = true
          if (formData.crdsSettings['gslb.crds.retainOnDelete']) {
            formData.nsicSettings['nsic.crds.retainOnDelete'] = true
          } else {
            formData.nsicSettings['nsic.crds.retainOnDelete'] = false
          }
        } else {
          formData.nsicSettings['nsic.crds.install'] = false
        }
      }
    }

    var x = {};
    for (let group in formData) {
      for (let k in formData[group]) {
        var splits = k.split('.'); //e.g., exporter.ports.containerPorts
        var q = formData[group][k];
        if (q) { //sometimes it is 'undefined' leading to exceptions in safeDump
          var p = x;
          for (let t of splits.slice(0, -1)) {
            if (!(t in p)) {
              p[t] = {};
            }
            p = p[t];
          }
          p[splits.slice(-1)] = q;
        }
      }
    }
    if (formData.ipamSettings && !formData.ipamSettings['ipam.enabled']) {
      delete x.ipam;
    }

    if (formData.adcSettings && formData.nsoeConfig) {
      if (formData.nsoeConfig['nsic.nsoeConfig.required']) {
        if (!formData.nsoeConfig['nsic.nsoeConfig.transactions.enable']) {
          delete x.nsic.nsoeConfig.transactions;
        }
        if (!formData.nsoeConfig['nsic.nsoeConfig.timeseries.metrics.enable']) {
          delete x.nsic.nsoeConfig.timeseries.metrics;
        }
        if (!formData.nsoeConfig['nsic.nsoeConfig.distributedTracing.enable']) {
          delete x.nsic.nsoeConfig.distributedTracings;
        }
      } else {
        delete x.nsic.nsoeConfig;
      }

    }
    if (formData.cloudSettings) {
      if (!formData.cloudSettings['cpx.aws']) {
        delete x.cpx.serviceAnnotations
      }
    }
    if (formData.cpxSettings && formData.cpxnsoeConfig) {
      if (formData.cpxnsoeConfig['cpx.nsoeConfig.required']) {
        if (!formData.cpxnsoeConfig['cpx.nsoeConfig.transactions.enable']) {
          delete x.cpx.nsoeConfig.transactions;
        }
        if (!formData.cpxnsoeConfig['cpx.nsoeConfig.timeseries.metrics.enable']) {
          delete x.cpx.nsoeConfig.timeseries.metrics;
        }
        if (!formData.cpxnsoeConfig['cpx.nsoeConfig.distributedTracing.enable']) {
          delete x.cpx.nsoeConfig.distributedTracing;
        }
      } else {
        delete x.cpx.nsoeConfig;
      }
    }
    if (formData.exporterSettings) {
      if (x.nsic && x.nsic.exporter && !x.nsic.exporter.required) {
        delete x.nsic.exporter;
      }
      if (x.cpx && x.cpx.exporter && !x.cpx.exporter.required) {
        delete x.cpx.exporter;
      }
      if (x.iaIngress && x.iaIngress.metricExporter && !x.iaIngress.metricExporter.required) {
        delete x.iaIngress.metricExporter
      }
      if (x.iaEgress && x.iaEgress.metricExporter && !x.iaEgress.metricExporter.required) {
        delete x.iaEgress.metricExporter
      }
    }

    if (formData.nsncSettings && !formData.nsncSettings['nsnc.enabled']) {
      delete x.nsnc;
    }
    if (formData.nsncSettings && formData.nsncSettings['nsnc.enabled'] && formData.nsicSettings) {
      delete x.nsic.nodeWatch;
    }
    if (formData.ingressGatewaySettings) {
      if (!formData.ingressGatewaySettings['iaIngress.netscalerCPX']) {
        delete x.iaIngress.ADMSettings;
        delete x.iaIngress.ingressGateway.image;
        delete x.iaIngress.ingressGateway.mgmtHttpPort;
        delete x.iaIngress.ingressGateway.mgmtHttpsPort;
        delete x.iaIngress.ingressGateway.httpNodePort;
        delete x.iaIngress.ingressGateway.httpsNodePort;
        delete x.iaIngress.ingressGateway.lightWeightCPX;
        if (formData.ingressGatewaySettings['iaIngress.ingressGateway.tcpPort']) {
          for (var index in x.iaIngress.ingressGateway.tcpPort) {
            if (x.iaIngress.ingressGateway.tcpPort[index]['Node Port']) {
              delete x.iaIngress.ingressGateway.tcpPort[index]['Node Port'];
            }
          }
        }
      }
      if (formData.ingressGatewaySettings['iaIngress.ingressGateway.secretVolume']) {
        var secret_vol_list_dict = formData.ingressGatewaySettings['iaIngress.ingressGateway.secretVolume']
        var display_secret_vol_list = []
        for (var i in secret_vol_list_dict) {
          console.log(secret_vol_list_dict[i])
          var secret_vol_dict = {}
          if (secret_vol_list_dict[i]['Volume Name']) {
            secret_vol_dict['name'] = secret_vol_list_dict[i]['Volume Name']
          }
          if (secret_vol_list_dict[i]['Secret Name']) {
            secret_vol_dict['secretName'] = secret_vol_list_dict[i]['Secret Name']
          }
          if (secret_vol_list_dict[i]['Mount Path']) {
            secret_vol_dict['mountPath'] = secret_vol_list_dict[i]['Mount Path']
          }
          display_secret_vol_list.push(secret_vol_dict)
        }
        x.iaIngress.ingressGateway.secretVolume = display_secret_vol_list
      }
      if (formData.ingressGatewaySettings['iaIngress.ingressGateway.tcpPort']) {
        var tcp_port_list_dict = formData.ingressGatewaySettings['iaIngress.ingressGateway.tcpPort']
        var display_tcp_port_list = []
        for (i in tcp_port_list_dict) {
          var port_dict = {}
          if (tcp_port_list_dict[i]['Port Name']) {
            port_dict['name'] = tcp_port_list_dict[i]['Port Name']
          }
          if (tcp_port_list_dict[i]['Node Port']) {
            port_dict['nodePort'] = tcp_port_list_dict[i]['Node Port']
          }
          if (tcp_port_list_dict[i]['Port']) {
            port_dict['port'] = tcp_port_list_dict[i]['Port']
          }
          if (tcp_port_list_dict[i]['Target Port']) {
            port_dict['targetPort'] = tcp_port_list_dict[i]['Target Port']
          }
          display_tcp_port_list.push(port_dict)
        }
        x.iaIngress.ingressGateway.tcpPort = display_tcp_port_list
      }

      if (formData.ingressGatewaySettings['iaIngres.netscalerCPX']) {
        delete x.nsnc;
      }
    }

    if (formData.nsoeSettings && formData.nsoeSettings['nsoe.enabled']) {
      if (!formData.nsoeSettings['nsoe.ns_tracing.enabled']) {
        delete x.nsoe.ns_tracing;
      }
      if (!formData.nsoeSettings['nsoe.kafka.enabled']) {
        delete x.nsoe.kafka;
      }
      if (!formData.nsoeSettings['nsoe.elasticsearch.enabled']) {
        delete x.nsoe.elasticsearch;
      }
      if (!formData.nsoeSettings['nsoe.timeseries.enabled']) {
        delete x.nsoe.timeseries;
      }
    }
    if (formData.nsoeSettings && !formData.nsoeSettings['nsoe.enabled']) {
      delete x.nsoe;
    }

    if (formData.nsicSettings) {
      var nsic_ingress_list = formData.nsicSettings['nsic.ingressClass']
      var nsic_ingress_classes = []
      for (var value in nsic_ingress_list) {
        var dict = nsic_ingress_list[value]
        for (var key in dict) {
          if (dict.hasOwnProperty(key)) {
            if (dict[key]) {
              nsic_ingress_classes.push(dict[key])
            }
          }
        }
      }
      x.nsic.ingressClass = nsic_ingress_classes
      x.nsic.logLevel = formData.nsicSettings['nsic.logLevel']["logLevel"]
    }

    if (formData.gslbnsicSettings) {
      x.gslb.logLevel = formData.gslbnsicSettings['gslb.logLevel']["logLevel"]
    }

    if (formData.siteSettings) {
      if (formData.siteSettings['gslb.sitedata']) {
        var site_data_list_dict = formData.siteSettings['gslb.sitedata']
        var display_site_data_list = []
        for (i in site_data_list_dict) {
          var site_dict = {}
          if (site_data_list_dict[i]['siteName']) {
            site_dict['siteName'] = site_data_list_dict[i]['siteName']
          }
          if (site_data_list_dict[i]['secretName']) {
            site_dict['secretName'] = site_data_list_dict[i]['secretName']
          }
          if (site_data_list_dict[i]['siteIp']) {
            site_dict['siteIp'] = site_data_list_dict[i]['siteIp']
          }
          if (site_data_list_dict[i]['siteRegion']) {
            site_dict['siteRegion'] = site_data_list_dict[i]['siteRegion']
          }
          display_site_data_list.push(site_dict)
        }
        x.gslb.sitedata = display_site_data_list
      }
    }

    if (formData.cpxDeploymentSettings) {
      var cpx_ingress_list = formData.cpxDeploymentSettings['cpx.ingressClass']
      var cpx_ingress_classes = []
      for (var cpx_value in cpx_ingress_list) {
        var cpx_dict = cpx_ingress_list[cpx_value]
        for (var cpx_key in cpx_dict) {
          if (cpx_dict.hasOwnProperty(cpx_key)) {
            if (cpx_dict[cpx_key]) {
              cpx_ingress_classes.push(cpx_dict[cpx_key])
            }
          }
        }
      }
      x.cpx.ingressClass = cpx_ingress_classes
      x.cpx.logLevel = formData.cpxDeploymentSettings['cpx.logLevel']["logLevel"]
    }


    if (formData.adcSettings) {
      x.nsic.nsProtocol = formData.adcSettings['nsic.nsProtocol']['nsProtocol']
    }

    if (formData.nsicAdvancedSettings) {
      if (formData.nsicAdvancedSettings['nsic.nsicSettings.required']) {
        console.log(formData)
        x.nsic.nsicSettings.nsicConfig.NS_HTTP2_SERVER_SIDE = formData.nsicAdvancedSettings['nsic.nsicSettings.nsicConfig.NS_HTTP2_SERVER_SIDE']['NS_HTTP2_SERVER_SIDE']
        x.nsic.nsicSettings.nsicConfig.NS_COOKIE_VERSION = formData.nsicAdvancedSettings['nsic.nsicSettings.nsicConfig.NS_COOKIE_VERSION']['NS_COOKIE_VERSION']
      } else if (!formData.nsicAdvancedSettings['nsic.nsicSettings.required']) {
        delete x.nsic.nsicSettings
      }
    }

    if (formData.cpxnsicAdvancedSettings) {
      if (formData.cpxnsicAdvancedSettings['cpx.nsicSettings.required']) {
        x.cpx.nsicSettings.nsicConfig.NS_HTTP2_SERVER_SIDE = formData.cpxnsicAdvancedSettings['cpx.nsicSettings.nsicConfig.NS_HTTP2_SERVER_SIDE']['NS_HTTP2_SERVER_SIDE']
        x.cpx.nsicSettings.nsicConfig.NS_COOKIE_VERSION = formData.cpxnsicAdvancedSettings['cpx.nsicSettings.nsicConfig.NS_COOKIE_VERSION']['NS_COOKIE_VERSION']
      } else if (!formData.nsicAdvancedSettings['cpx.nsicSettings.required']) {
        delete x.cpx.nsicSettings
      }
    }

    if (formData.gslbadcSettings) {
      x.gslb.nsProtocol = formData.gslbadcSettings['gslb.nsProtocol']['nsProtocol']
    }
    if (formData.gslbnsicSettings) {
      x.gslb.pullPolicy = formData.gslbnsicSettings['gslb.pullPolicy']["pullPolicy"]
    }
    if (formData.cpxSettings) {
      x.cpx.pullPolicy = formData.cpxSettings['cpx.pullPolicy']["pullPolicy"]
      x.cpx.nsic.pullPolicy = formData.cpxSettings['cpx.nsic.pullPolicy']["pullPolicy"]
    }

    if (formData.nsicSettings) {
      if (formData.nsicSettings['nsic.pullPolicy']) {
        x.nsic.pullPolicy = formData.nsicSettings['nsic.pullPolicy']["pullPolicy"]
      }
      if (formData.nsicSettings['cpx.nsic.pullPolicy']) {
        x.cpx.nsic.pullPolicy = formData.nsicSettings['cpx.nsic.pullPolicy']["pullPolicy"]
      }
    }

    if (formData.exporterSettings) {
      if (formData.exporterSettings['nsic.exporter.required']) {
        x.nsic.exporter.pullPolicy = formData.exporterSettings['nsic.exporter.pullPolicy']["pullPolicy"]
      }
    }

    if (formData.cpxexporterSettings) {
      if (formData.cpxexporterSettings['cpx.exporter.required']) {
        x.cpx.exporter.pullPolicy = formData.cpxexporterSettings['cpx.exporter.pullPolicy']["pullPolicy"]
      }
    }

    if (formData.nsncSettings) {
      if (formData.nsncSettings['nsnc.enabled']) {
        if (formData.nsncSettings['nsnc.pullPolicy']) {
          x.nsnc.pullPolicy = formData.nsncSettings['nsnc.pullPolicy']["pullPolicy"]
        }
      }
    }

    if (formData.nsoeSettings) {
      if (formData.nsoeSettings['nsoe.enabled']) {
        /*if (formData.nsoeSettings['nsoe.license.accept']) {
          x.nsoe.license.accept = formData.nsoeSettings['nsoe.license.accept']["license"]
        }*/
        if (formData.nsoeSettings['nsoe.pullPolicy']) {
          x.nsoe.pullPolicy = formData.nsoeSettings['nsoe.pullPolicy']["pullPolicy"]
        }
      }
    }

    if (formData.ipamSettings) {
      if (formData.ipamSettings['ipam.enabled']) {
        if (formData.ipamSettings['ipam.pullPolicy']) {
          x.ipam.pullPolicy = formData.ipamSettings['ipam.pullPolicy']["pullPolicy"]
        }
        if (formData.ipamSettings['ipam.vipRange']) {
          var vip_str = '['
          var vip_array = []
          if (formData.ipamSettings['ipam.vipRange'].indexOf(',') > -1) {
            vip_array = formData.ipamSettings['ipam.vipRange'].split(',')
          }
          else {
            vip_array = [formData.ipamSettings['ipam.vipRange']]
          }
          for (var vip_index in vip_array) {
            vip_str = vip_str + '"' + vip_array[vip_index] + '",'
          }
          x.ipam.vipRange = vip_str.slice(0, -1) + ']'
        }
      }
    }

    var yamlStr = yaml.safeDump(x);
    console.log('toYaml: ' + yamlStr);
    this.props.setParentState(this.props.groupId, this.props.formId, formData, yamlStr);
  }


  copyYaml(yamlStr) {
    var copyText = yamlStr;
    copyText.select();
    copyText.setSelectionRange(0, 99999)
    document.execCommand("copy");
  }

  downloadYaml = () => {
    var FileSaver = require('file-saver');

    var blob = new File([this.props.yamlStr], "values.yaml", { type: "text/plain;charset=utf-8" });
    FileSaver.saveAs(blob);
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    console.log('componentDidUpdate prevProps=', prevProps)
    console.log('componentDidUpdate currProps=', this.props)

  }

  render() {
    return (
      <>
        <Grid.Column width={7} style={{ height: '600px', overflow: 'scroll' }}>
          <Form
            schema={this.props.schema}
            noHtml5Validate
            formData={this.props.formData}
            liveValidate={true}
            showErrorList={false}
            uiSchema={this.props.uischema}
            onChange={this.toYaml}
            onSubmit={this.toYaml}
            onError={log('errors')}
          >
            <br />
          </Form>

        </Grid.Column>
        <Grid.Column width={9} style={{ height: '600px', overflow: 'scroll' }}>
          <Header>
            Values.yaml
                </Header>
          <SyntaxHighlighter language="yaml" style={a11yDark}>
            {this.props.yamlStr}
          </SyntaxHighlighter>
          <CopyToClipboard text={this.props.yamlStr}
            onCopy={() => this.setState({ copied: true })}>
            <button className="ui button">
              <i className="copy icon"></i>
              Copy
              </button>
          </CopyToClipboard>
          <button className="ui button" onClick={this.downloadYaml}>
            <i className="download icon"></i>
            Download
              </button>
          <div>
            <Segment vertical>
              Use the downloaded values.yaml with helm to install the NetScaler Cloud Native stack.
                    </Segment>
            <Segment inverted >helm repo add netscaler https://netscaler.github.io/netscaler-helm-charts/<br />
              helm install "name" netscaler/citrix-cloud-native -f values.yaml <br />
            </Segment>
          </div>

        </Grid.Column>
      </>
    );
  }
}

export default HelmForm;
