import React from 'react';
import { Accordion, Menu, Popup, Image } from 'semantic-ui-react';



class SidebarItems extends React.Component {
    state = { activeAccordionIndex: 0, activeItem: 0, selectedItem: '' }

    handleAccordionClick = (e, titleProps) => {
        const { index } = titleProps
        const activeAccordionIndex  = this.state.activeAccordionIndex;
        const newIndex = activeAccordionIndex === index ? -1 : index

        this.setState({ selectedItem: '', activeAccordionIndex: newIndex, activeItem: 0 })
    }

    handleItemClick = (e, itemProps) => {
        const { index, name } = itemProps
        console.log('Item click index= ' + index + ', name=' + name)
        const activeAccordionIndex  = this.state.activeAccordionIndex;
        this.setState({ selectedItem: name, activeAccordionIndex: activeAccordionIndex, activeItem: index })
        this.props.onClick(activeAccordionIndex, index)
    }

    render() {
        const activeAccordionIndex  = this.state.activeAccordionIndex;
        const selectedItem = this.state.selectedItem;
        var i = -1;
        var j = this.state.activeItem;
        return (
            <Accordion as={Menu} vertical secondary fluid exclusive={false} className="global-left-nav">
                {this.props.config.map(((cfg) => {
                    i++;
                    j = -1;
                    return (
                        <Menu.Item position='left'>
                            <Popup
                                position="top center"
                                trigger={
                                    <Accordion.Title
                                        active={activeAccordionIndex === i}
                                        content={<span>{cfg.groupTitle}</span>}
                                        index={i}
                                        onClick={this.handleAccordionClick}
                                        left
                                    />}
                            >
                                <Popup.Header>{cfg.groupTooltip}</Popup.Header>
                                <Popup.Content>
                                    <Image src={cfg.groupImage} />
                                </Popup.Content>
                            </Popup>
                        {cfg.forms && 
                            <Accordion.Content active={activeAccordionIndex === i}>
                                {cfg.forms.map(((item) => {
                                    j++;
                                    return (
                                        <Popup
                                            position="top center"
                                            trigger={
                                                <Menu.Item index={j} key={item.title} name={item.title} active={selectedItem === item.title} onClick={this.handleItemClick}>
                                                    {item.title}
                                                </Menu.Item>
                                            }>
                                            {item.itemTooltip}
                                        </Popup>
                                    );
                                }))}
                            </Accordion.Content>
                        }
                    </Menu.Item>
                    );
                }))}
            </Accordion>
        );

    }
}

export default SidebarItems;
