function Copyright() {
    return (
        <Segment attached={'bottom'} basic padded className='ut_border_none' style={{ marginLeft: '1.4rem' }}>
            {'Copyright © '}
            <Item as={'a'} href="https://citrix.com/">
                NetScaler Systems
            </Item>{' '}
            {new Date().getFullYear()}
            {'.'}
        </Segment>
    );
}

class MainContent extends React.Component {

    //({ description, groupId, formId, schema, uischema, formData, yamlStr, setParentState }) 
    constructor(props) {
        super(props);
        this.panels = [
            {
                key: 'prerequisite',
                title: 'Prerequisites',
                content: [
                    this.props.description
                ]
            },

        ]
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.description !== this.props.description) {
            this.panels = [
                {
                    key: 'prerequisite',
                    title: 'Prerequisites',
                    content: [
                        this.props.description
                    ]
                },
            ]
        }
    }
    render() {
        return (
            <>
                <Accordion panels={this.panels} />
                <Grid.Row>
                    <HelmForm
                        groupId={this.props.groupId}
                        formId={this.props.formId}
                        schema={this.props.schema}
                        uischema={this.props.uischema}
                        formData={this.props.formData}
                        yamlStr={this.props.yamlStr}
                        setParentState={this.props.setParentState}
                    />
                </Grid.Row>
            </>
        );
    }
}

