    itemClick = (groupId, formId, e) => {
        console.log('Clicked on ' + groupId + ' formId= ' + formId);
        this.setState({ groupId: groupId })
        this.setState({ formId: formId })
    }


    setParentState = (groupIndex, formIndex, data, yamlStr) => {
        this.setState(((prevState, props) => {
            prevState.config[groupIndex].forms[formIndex]['formData'] = data;
            prevState.config[groupIndex].forms[formIndex]['yamlStr'] = yamlStr;
            return { config: prevState.config }
        }))

        console.log('setParentState Group index= ' + groupIndex + ', Form index= ' + formIndex);
        console.log('setParentState Form data= ', this.state.config[groupIndex].forms[formIndex]['formData']);
        console.log('setParentState Yaml: ', yamlStr);
    }


